import { followAPI, userAPI } from "../API/api";
import { updateObjectInArray } from "../utils/objects-helper";

const FOLLOW = "my-app/users/follow";
const UNFOLLOW = "my-app/users/unfollow";
const SET_USERS = "my-app/users/set-users";
const SET_CURRENT_PAGE = "my-app/users/set-currnet-page";
const SET_TOTAL_USERS_COUNT = "my-app/users/set-total-users-count";
const SET_FETCHING = "my-app/users/set-fetching";
const TOOGLE_FOLLOWING_IN_PROCESS = "my-app/users/TOOGLE-FOLLOWING-IN-PROCESS";

const initialState = {
  users: [],
  totalUsers: 0,
  countUsersOnPage: 100,
  currentPage: 1,
  isFetching: false,
  followingInProcess: [],
};

const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {
          followed: false,
        }),
      };
    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {
          followed: true,
        }),
      };
    case SET_USERS:
      return {
        ...state,
        users: action.users,
      };
    case SET_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.currentPage,
      };
    case SET_TOTAL_USERS_COUNT:
      return {
        ...state,
        totalUsers: action.totalUsersCount,
      };
    case SET_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching,
      };
    case TOOGLE_FOLLOWING_IN_PROCESS:
      return {
        ...state,
        followingInProcess: action.isFetching
          ? [...state.followingInProcess, action.userId]
          : state.followingInProcess.filter((id) => id !== action.userId),
      };
    default:
      return state;
  }
};

const followAccept = (userId) => {
  return {
    type: UNFOLLOW,
    userId,
  };
};
const unfollowAccept = (userId) => {
  return {
    type: FOLLOW,
    userId,
  };
};
const setUsers = (users) => {
  return {
    type: SET_USERS,
    users,
  };
};
const setTotalUsersCount = (totalUsersCount) => {
  return {
    type: SET_TOTAL_USERS_COUNT,
    totalUsersCount,
  };
};
const toggleFollowingInProcess = (isFetching, userId) => {
  return {
    type: TOOGLE_FOLLOWING_IN_PROCESS,
    isFetching,
    userId,
  };
};
const setFetching = (isFetching) => {
  return {
    type: SET_FETCHING,
    isFetching,
  };
};
export const setCurretPage = (currentPage) => {
  return {
    type: SET_CURRENT_PAGE,
    currentPage,
  };
};

const followUnfollowFlow = async (dispatch, userId, apiFunc, actionCreator) => {
  dispatch(toggleFollowingInProcess(true, userId));
  const data = await apiFunc(userId);
  if (data.resultCode === 0) dispatch(actionCreator(userId));
  dispatch(toggleFollowingInProcess(false, userId));
};

export const follow = (userId) => (dispatch) => {
  followUnfollowFlow(
    dispatch,
    userId,
    followAPI.follow.bind(followAPI),
    followAccept
  );
};

export const unfollow = (userId) => (dispatch) => {
  followUnfollowFlow(
    dispatch,
    userId,
    followAPI.unfollow.bind(followAPI),
    unfollowAccept
  );
};

export const getCurrentPageUsers =
  (pageNumber, countUsersOnPage) => async (dispatch) => {
    dispatch(setFetching(true));

    const data = await userAPI.getUsers(pageNumber, countUsersOnPage);

    dispatch(setFetching(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
  };

export default usersReducer;
