//import * as axios from "axois";
import axios from "axios";

const instance = axios.create({
  baseURL: "https://social-network.samuraijs.com/api/1.0/",
  withCredentials: true,
  headers: {
    "API-KEY": "35b32bc7-c48b-411e-ac15-98f2b5bd9466",
  },
});

export const userAPI = {
  getUsers(currentPage = 1, countUsersOnPage = 10) {
    return instance
      .get(`users?page=${currentPage}&count=${countUsersOnPage}`)
      .then((response) => response.data);
  },
  getProfile(userId) {
    console.warn("Obsolete method. Please, profileAPI object");
    return profileAPI.getUsers(userId);
  },
};

export const followAPI = {
  follow(userId) {
    return instance.post(`follow/${userId}`).then((response) => response.data);
  },
  unfollow(userId) {
    return instance
      .delete(`follow/${userId}`)
      .then((response) => response.data);
  },
};

export const authAPI = {
  login() {
    return instance.get("auth/me");
  },
  setLogin(email, password, rememberMe) {
    return instance.post(`/auth/login`, { email, password, rememberMe });
  },
  Logout() {
    return instance.delete(`/auth/login`);
  },
};

export const profileAPI = {
  getProfile(userId) {
    return instance.get("profile/" + userId);
  },
  getStatus(userId) {
    return instance.get("profile/status/" + userId);
  },
  updateStatus(status) {
    return instance.put("profile/status", { status });
  },
};
