const ADD_MESSAGE = "my-app/dialogs/ADD-MESSAGE";

const initialState = {
  dialogs: [
    { id: 1, name: "Evgeniy" },
    { id: 2, name: "Misha" },
    { id: 3, name: "Serezha" },
    { id: 4, name: "Pasha" },
    { id: 5, name: "Evgen" },
  ],

  messages: [
    { id: 1, message: "Hi" },
    { id: 2, message: "How are you" },
    { id: 3, message: "Yo" },
    { id: 4, message: "Yo" },
    { id: 5, message: "Yo" },
  ],
  newMessageText: "",
};
const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE:
      return {
        ...state,
        newMessageText: "",
        messages: [
          ...state.messages,
          { id: 6, message: action.newMessageText },
        ],
      };

    default:
      return state;
  }
};

export const addMessageActionCreator = (newMessageText) => {
  return {
    type: ADD_MESSAGE,
    newMessageText,
  };
};

export default dialogsReducer;
