import { profileAPI } from "../API/api";

const ADD_POST = "my-app/profile/ADD-POST";
const SET_PROFILE = "my-app/profile/SET-PROFILE";
const SET_STATUS = "my-app/profile/SET-STATUS";
const DELETE_POST = "my-app/profile/DELETE-POST";

const initialState = {
  posts: [
    { id: 1, message: "How are you", likesCount: 12 },
    { id: 2, message: "My first post", likesCount: 1 },
  ],
  status: "",
  profile: null,
};
const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      return {
        ...state,
        posts: [
          ...state.posts,
          { id: 3, message: action.newPostText, likesCount: 0 },
        ],
      };
    }
    case SET_PROFILE:
      return {
        ...state,
        profile: action.profile,
      };
    case SET_STATUS:
      return {
        ...state,
        status: action.status,
      };
    case DELETE_POST:
      return {
        ...state,
        posts: state.posts.filter((post) => post.id !== action.postId),
      };
    default:
      return state;
  }
};

export const addPostActionCreator = (newPostText) => {
  return {
    type: ADD_POST,
    newPostText,
  };
};

const setProfile = (profile) => ({
  type: SET_PROFILE,
  profile,
});
const setStatus = (status) => {
  return {
    type: SET_STATUS,
    status,
  };
};
export const deletePost = (postId) => ({
  type: DELETE_POST,
  postId,
});

export const getUserProfile = (userId) => async (dispatch) => {
  const response = await profileAPI.getProfile(userId);
  return dispatch(setProfile(response.data));
};
export const getUserStatus = (userId) => async (dispatch) => {
  const response = await profileAPI.getStatus(userId);
  dispatch(setStatus(response.data));
};
export const updateUserStatus = (status) => async (dispatch) => {
  const response = await profileAPI.updateStatus(status);
  if (response.data.resultCode === 0) dispatch(setStatus(status));
};

export default profileReducer;
